    /**
     * unique viz key
     */
    key: 'splunk.image',
    /**
     * viz name
     */
    name: 'Image',
    category: VizCategory.MISC,
    /**
     * viz icon
     */
    icon: ImageIcon,
    dataContract,
    size,
    defaultContext,
    optionsSchema,
    editorConfig,
    supports: [VizBehavior.DYNAMIC_OPTIONS, VizBehavior.PLACEHOLDER],
    themes,
